"use client";

import { Recipe } from "@/lib/data/types";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";

interface ShoppingListProps {
  recipes: Recipe[];
}

interface IngredientGroup {
  [key: string]: number;
}

export function ShoppingList({ recipes }: ShoppingListProps) {
  const groupIngredients = (recipes: Recipe[]): IngredientGroup => {
    const ingredients: IngredientGroup = {};

    recipes.forEach((recipe) => {
      recipe.ingredients.forEach((ingredient) => {
        // Extraer la cantidad y la unidad si existe
        const match = ingredient.match(
          /^([\d.]+)?\s*(g|ml|unidad(?:es)?|pieza(?:s)?|cucharada(?:s)?|taza(?:s)?|)?\s*(.+)$/i
        );

        if (match) {
          const [, quantity, unit, name] = match;
          const cleanName = name.toLowerCase().trim();

          if (quantity) {
            ingredients[cleanName] =
              (ingredients[cleanName] || 0) + parseFloat(quantity);
          } else {
            ingredients[cleanName] = (ingredients[cleanName] || 0) + 1;
          }
        } else {
          const cleanName = ingredient.toLowerCase().trim();
          ingredients[cleanName] = (ingredients[cleanName] || 0) + 1;
        }
      });
    });

    return ingredients;
  };

  const groupedIngredients = groupIngredients(recipes);

  return (
    <Card>
      <CardHeader>
        <CardTitle>Lista de Compras</CardTitle>
        <CardDescription>
          Ingredientes necesarios para las recetas seleccionadas
        </CardDescription>
      </CardHeader>
      <CardContent>
        <ul className='space-y-2'>
          {Object.entries(groupedIngredients).map(([ingredient, quantity]) => (
            <li key={ingredient} className='flex items-center justify-between'>
              <span className='capitalize'>{ingredient}</span>
              <span className='text-muted-foreground'>
                {quantity % 1 === 0 ? quantity : quantity.toFixed(1)}
              </span>
            </li>
          ))}
        </ul>
      </CardContent>
    </Card>
  );
}
